<template>
  <div class="body-bg">
    <headerBar :header-data="headerData" />
    <div class="login-h">专家注册</div>
    <div class="login-h-mini">欢迎注册账号</div>
    <div class="login-form">

      <van-cell-group>
        <div class="input-item">
          <van-field
            ref="name"
            v-model="ztmc"
            v-validate="'required'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入专家姓名"
            title="专家名称"
            name="zjmc"
          />
          <p class="input-item-msg">{{ errors.first('zjmc') }}</p>
        </div>

        <div class="input-item">
          <van-field
            ref="name"
            v-model="lxrdh"
            v-validate="'required|phone'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入手机号码"
            title="手机号码"
            name="sjhm"
            @keyup="checkSjh()"
          />
          <p v-if="sjhState" class="input-item-msg">手机号已注册</p>
          <p v-if="!sjhState" class="input-item-msg">{{ errors.first('sjhm') }}</p>
        </div>

        <div class="input-item">
          <van-field
            ref="id"
            v-model="identifyCode"
            v-validate="'required|msgCode'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入验证码"
            title="验证码"
            name="yzm"
            @blur="checkYzm()"
          />
          <div class="input-operator mini">
            <van-button v-show="!computedTime" plain round size="mini" type="info" @click.prevent="getVerifyCode">
              获取验证码
            </van-button>
            <van-button v-show="computedTime" plain round size="mini" type="info" @click.prevent>
              已发送({{ computedTime }}s)
            </van-button>
          </div>
          <p v-if="yzmts" class="input-item-msg">验证码错误，请重新输入</p>
          <p v-if="!yzmts" class="input-item-msg">{{ errors.first('yzm') }}</p>
        </div>

        <div class="input-item password">
          <van-field
            ref="id"
            v-model="mm"
            v-validate="'required|mmLength'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入登录密码"
            title="登录密码"
            :type="eyesChange ==true?'text':'password'"
            name="mm"
            @blur="checkMm()"
          />

          <!-- 显示与隐藏密码 -->
          <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconkejian" />
          </div>
          <div v-else class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconbukejian" />
          </div>

          <p class="input-item-msg">{{ errors.first('mm') }}</p>
        </div>
        <div class="input-item password">
          <van-field
            ref="id"
            v-model="cfmm"
            v-validate="'required'"
            clearable
            is-highlight
            is-title-latent
            placeholder="重复密码"
            title="再次输入密码"
            :type="eyesChange ==true?'text':'password'"
            name="cfmm"
            @blur="checkMm()"
          />
          <!-- 显示与隐藏密码 -->
          <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconkejian" />
          </div>
          <div v-else class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconbukejian" />
          </div>

          <p v-if="mmtsState" class="input-item-msg">两次密码不一致</p>
          <p v-if="!mmtsState" class="input-item-msg">{{ errors.first('cfmm') }}</p>
        </div>

        <div class="input-item">
          <van-field
            ref="name"
            v-model="email"
            v-validate="'required|email'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入邮箱"
            title="邮箱"
            name="yx"
          />
          <p class="input-item-msg">{{ errors.first('yx') }}</p>
        </div>

        <div class="input-item">
          <van-field
            ref="name"
            v-model="ztdm"
            v-validate="'required|sfzLength'"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入身份证号码,所有字符大写"
            title="身份证号码"
            name="sfzh"
          />
          <p class="input-item-msg">{{ errors.first('sfzh') }}</p>
        </div>

        <div class="input-item">
          <div class="page-list block" style="align-items: inherit;">
            <div class="pagelist-left">身份证扫描件</div>
            <div class="pagelist-right">
              <van-uploader v-model="fileList" v-validate="'required'" name="smj" multiple max-count="2" min-count="2" /></div>
          </div>

          <p class="input-item-msg">{{ errors.first('smj') }}</p>
        </div>

        <div class="agreement-item flex">
          <van-checkbox v-model="checked" :disabled="disabled" @click="isRead()" />
          <div class="text">我已认真阅读并同意 <span class="blue" @click="goGysZcxy()">《服务协议》</span></div>
        </div>
        <p v-if="fwxyts" class="input-item-msg">请阅读并同意服务协议</p>

        <div class="login-button">
          <van-button round type="info" @click="handleSubmit()">注册</van-button>
        </div>
      </van-cell-group>
    </div>
    <div class="common-space" />
  </div>

</template>

<script>
import headerBar from '@/components/header/headerBar'
// import alertTip from '../../../components/common/alertTip'
// import SIdentify from '../../../components/identify/identify' //本地图片验证码
import { mapActions } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'
import { zjzc, checksjh } from '@/store/module.js'
export default {
  name: 'Zjzc',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      yzmts: false,
      fwxyts: false,
      mmtsState: false,
      sjhState: false,
      disabled: true, // 是否阅读服务协议
      checked: false,
      eyesChange: false, // 是否展示密码
      profiletitle: '',
      computedTime: 0, // 倒数记时
      ztmc: '', // 公司名称
      ztlb: '', // 法人类型(1-自然人21-供应商22-代理机构23-采购人)
      ztdm: '', // 供应商代码
      lxr: '', // 联系人
      lxrdh: '', // 联系电话
      mm: '', // 密码
      cfmm: '', // 重复密码
      email: '', // 邮箱
      ggemail: '', // 公共邮箱
      captchaCodeImg: '', // 验证码地址
      codeNumber: '', // 验证码
      phoneNumber: '', // 手机号
      identifyCodes: '111111', // 本地验证码
      identifyCode: '', // 本地验证码
      headerData: {
        title: '注册',
        leftUrl: 'back',
        leftText: '登录'
      },
      fileList: [
        // { url: 'https://img.yzcdn.cn/vant/leaf.jpg' },
        // Uploader 根据文件后缀来判断是否为图片文件
        // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
        // { url: 'https://img.yzcdn.cn/vant/leaf.jpg' }
      ]
    }
  },

  created() {
    this.disabled = this.$store.state.disabled
  },
  methods: {
    ...mapActions([
      'zjzc', 'sendMessage'
    ]),
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    checkYzm() {
      if (this.codeNumber !== this.identifyCode) {
        this.yzmts = true
      } else {
        this.yzmts = false
      }
    },
    checkMm() {
      if (this.mm !== this.cfmm) {
        this.mmtsState = true
      } else {
        this.mmtsState = false
      }
    },
    checkSjh() {
      if (!(/^1[3456789]\d{9}$/.test(this.lxrdh))) {
        return false
      }
      checksjh.data = this.lxrdh
      this.asyncApiModule(checksjh).then(res => {
        console.log('res', res)
        if (res === true) {
          this.sjhState = false
          return false
        } else {
          this.sjhState = true
          return true
        }
      })
    },
    isRead() {
      if (this.disabled === true) {
        this.$toast('请先阅读协议')
      } else {
        return
      }
    },
    /* 专家注册*/
    handleSubmit() {
      this.$validator.validate().then(result => {
        if (this.codeNumber !== this.identifyCode) {
          this.yzmts = true
        } else {
          this.yzmts = false
        }
        if (this.checked === false) {
          this.fwxyts = true
        } else {
          this.fwxyts = false
        }
        if (this.fwxyts === true || this.yzmts === true) {
          return false
        }
        if (!result) {
          // this.$toast.fail('数据格式有误')
          // this.$message.warning(this.errors.all()[0])
        } else {
          console.log('fileList======', this.fileList)
          const list = []
          if (this.fileList.length === 1) {
            list.push(this.fileList[0].content)
          } else {
            list.push(this.fileList[0].content)
            list.push(this.fileList[1].content)
          }
          console.log('list======', list)
          zjzc.data = {
            ztmc: this.ztmc,
            ztdm: this.ztdm,
            lxrdh: this.lxrdh,
            mm: this.mm,
            email: this.email,
            fileList: list
          }
          this.asyncApiModule(zjzc).then(res => {
            this.$toast.fail('注册成功')
            this.$router.push({
              name: 'GysZcSuccess'
            })
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },

    // 获取短信验证码
    getVerifyCode() {
      if (!(/^1[3456789]\d{9}$/.test(this.lxrdh))) {
        return false
      }
      if (this.sjhState === true) {
        return false
      }
      this.computedTime = 60
      this.timer = setInterval(() => {
        this.computedTime--
        if (this.computedTime === 0) {
          clearInterval(this.timer)
        }
      }, 1000)
      this.sendMessage({
        sjh: this.lxrdh
      }).then(res => {
        this.codeNumber = res
      }).catch(error => {
        console.log(error)
      })
    },
    // 获取验证吗，线上环境使用固定的图片，生产环境使用真实的验证码
    getCaptchaCode() {
      // const res = await getcaptchas()
      // this.captchaCodeImg = res.code
    },
    // 随机数
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min)
    },
    // 刷新数据
    refreshCode() {
      this.identifyCode = ''
      this.makeCode(this.identifyCodes, 4)
    },
    // 产生数据
    makeCode(o, l) {
      // for (let i = 0; i < l; i++) {
      //   this.identifyCode += this.identifyCodes[
      //     this.randomNum(0, this.identifyCodes.length)
      //   ]
      // }
      // console.log(this.identifyCode)
    },

    /* 控制是否展示密码*/
    eyesCheck() {
      this.eyesChange = !this.eyesChange
    },
    eyesCheck1() {
      this.eyesChange1 = !this.eyesChange1
    },
    goGysZcxy() {
      this.disabled = false
      this.$router.push({
        name: 'GysZcxy'
      })
    }

  }

}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
